@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}
html {
  font-size: 62.5%;
}
body {
  height: 100vh;
  padding: 1rem;
  font-family: "Roboto", sans-serif;
  box-sizing: border-box;
}

body {
  background: linear-gradient(45deg, indigo, cyan, white, pink, red);
  background: #000;
}
.Calculator {
  display: flex;
  flex-direction: column;
  border: 1px solid #fff;
  height: calc(100vh - 2vh);
  border-radius: 1rem;
  padding: 1rem;
  background-color: rgba(255, 255, 255, 0.3);
  backdrop-filter: blur(0.2rem);
  -webkit-backdrop-filter: blur(0.2rem);
  background: #000;
}
.InputSection {
  height: 30%;
}
.InputSection h1 {
  font-size: 10rem;
  color: #fff;
  text-align: right;
}
.Input input {
  text-align: right;
  width: 100%;
  background-color: #000;
  color: #fff;
  border: none;
  font-size: 7rem;
}
.ButtonSection {
  height: 70%;
  display: flex;
  flex-direction: column;
  grid-gap: 1.5rem;
  gap: 1.5rem;
}
.m-Buttons {
  height: 100%;
  display: flex;
  grid-gap: 1rem;
  gap: 1rem;
}
.Button {
  width: 100%;
  border-radius: 50%;
  outline: none;
  border: none;
  border: 1px solid #fff;
  color: #fff;
  background-color: rgba(255, 255, 255, 0.3);
  backdrop-filter: blur(0.2rem);
  -webkit-backdrop-filter: blur(0.2rem);
}

